
footer{
    width: 100%;
    position: fixed;
    left: 0;
    bottom: 0;
    z-index: 66;
}
.foot{
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    font-family: 'iconfont';
    background: rgba(121, 134, 145, .96);
    color: #fff;
    
}
.foot div{
    -ms-flex: 1 1;
        flex: 1 1;
    padding: 14px 26px;
    text-align: center;
}
.foot dt{
    font-size: 36px;
    margin-bottom: 8px;
}
.foot dd{
    font-size: 26px;
}


