
header{
    text-align: center;
    min-height: 77px;
    line-height: 77px;
    background-color: #008acb;
    color:#fff;
    font-size: 36px;
    -webkit-transition: .2s;
    -o-transition: .2s;
    transition: .2s;
}
header h2{
    display: inline-block;
    margin-left: -66px;
    font-size: 36px;
    font-weight: normal;
    color: #fff;
}
.back{
    width: 50px;
    height: 50px;
    line-height: 46px;
    margin-left: 16px;
    margin-top: 14px;
    font-family: 'iconfont';
    float: left;
    font-size: 30px;
    font-weight: initial;
}


