
.mask{
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    background: rgba(0,0,0,.6);
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
}
.disb{
    top: 0;
}
.disn{
    top: 100%;
}
.wrap{
    width: 100%;
    position: absolute;
    left: 0;bottom: 0;
    z-index: 99;
}
.btns{
    padding: 0 30px;
    font-size: 30px;
    color: #fff;
    height: 67px;
    line-height: 67px;
    background: #7e8b95;
    font-weight: normal;
    display: -ms-flexbox;
    display: flex;
}
.ok{
    float: right;
}
.no,
.ok{
    -ms-flex-positive: 0;
        flex-grow: 0;
}
.btns p{
    height: 67px;
    text-align: center;
    -ms-flex-positive: 1;
        flex-grow: 1;
    font-size: 28px;
    opacity: .5;
}
.bottom{
    background: #fff;
    overflow: hidden;
    border: 1px solid #e3e3e3;
    margin-bottom: -1px;
}
.con{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    padding: 12px 30px 0;
    margin-right: -20px;
    padding-top: 12px;
}
#conMin p{
    width: 112px;
    margin-right: 40px;
}
#conMin{
    margin-right: -40px;
}
.bottom p{
    width: 182px;
    height: 50px;
    text-align: center;
    line-height: 50px;
    border: 1px solid #e8ecef;
    border-radius: 25px;
    margin-right: 16px;
    margin-bottom: 24px;
    font-size: 28px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.bottom p span{
    margin-left: 4px;
}
.bottom .active{
    background: #008acb;
    color: #fff;
    border: 0;
    -webkit-transition: .15s;
    -o-transition: .15s;
    transition: .15s;
}
.tit {
    height: 42px;
    overflow: hidden;
    text-align: center;
    padding: 6px 0;
    color: #909090;
    font-weight: normal;
}
.tit em{
    margin-top: 21px;
    margin-bottom: -21px;
    display: block;
    height: 1px;
    background: -webkit-gradient(linear, left top, right top, from(#fff), color-stop(#c3e3f3), to(#fff));
    background: -webkit-linear-gradient(left, #fff, #c3e3f3, #fff);
    background: -o-linear-gradient(left, #fff, #c3e3f3, #fff);
    background: linear-gradient(to right, #fff, #c3e3f3, #fff);
}
.tit span{
    display: inline-block;
    line-height: 42px;
    font-size: 22px;
    background: #fff;
    padding: 0 8px;
}
.shaixuan h6{
    height: 56px;
    line-height: 56px;
    text-align: center;
    font-size: 22px;
    border:1px solid #e3e3e3;
}
.shaixuan{
    border-bottom: 1px solid #e3e3e3;
}
.shaixuan h5,
.shaixuan + h5{
    height: 56px;
    line-height: 56px;
    color: #00234e;
    font-size: 24px;
    text-align: center;
}

.time{
    padding: 0 30px;
}
.time div{
    margin-right: -48px;
    padding: 14px 0;
}
.time span{
    display: inline-block;
    min-width: 262px;
    height: 48px;
    line-height: 48px;
    border: 1px solid #e3e3e3;
    border-radius: 25px;
    font-size: 28px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding-left: 20px;
    margin-right: 48px;
}

.reset{
    text-align: center;
    padding: 22px 0;
}
.reset button{
    padding: 13px 30px;
    background: #e5004a;
    color: #fff;
    font-size: 28px;
    border-radius: 27px;
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0); 
    outline: none;
}


