
.clearfix:before,.clearfix:after{content: '';display: block;}  
.clearfix:after{clear: both;}  

.fixed{
    width: 100%;
    position: fixed;
    left: 0;top: -174px;
    height: 77px;
    -webkit-animation: move .3s ease forwards;
            animation: move .3s ease forwards;
    overflow: hidden;
    z-index: 100;
}
@-webkit-keyframes move{
    0%{
        top: -174px;
    }
    100%{
        top: 0;
    }
}
@keyframes move{
    0%{
        top: -174px;
    }
    100%{
        top: 0;
    }
}

.search_wrap{
    padding: 10px 30px;
}
.search{
    width: 100%;
    height: 60px;
    text-indent: 66px;
    background: #e6f4fa url(../img/bg.png) no-repeat 24px center ;
    font-size: 24px;
    border-radius: 8px; 
}

.hide{
    top: 100%;
    visibility: hidden;
}
.show{
    top: 0;
    visibility: visible;
}
.hide,
.show{
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;
    background: #fff;
    -webkit-transition: .3s ease;
    -o-transition: .3s ease;
    transition: .3s ease;
    overflow-y: scroll;
    z-index: 999;
}

.tit {
    height: 42px;
    overflow: hidden;
    text-align: center;
    padding: 6px 0;
    color: #909090;
}
.tit em{
    margin-top: 21px;
    margin-bottom: -21px;
    display: block;
    height: 1px;
    background: -webkit-gradient(linear, left top, right top, from(#fff), color-stop(#c3e3f3), to(#fff));
    background: -webkit-linear-gradient(left, #fff, #c3e3f3, #fff);
    background: -o-linear-gradient(left, #fff, #c3e3f3, #fff);
    background: linear-gradient(to right, #fff, #c3e3f3, #fff);
}
.tit span{
    display: inline-block;
    line-height: 42px;
    font-size: 22px;
    background: #fff;
    padding: 0 8px;
}

.now{
    padding: 18px 30px;
    font-size: 26px;
}
.now i,
.now span{
    display: inline-block;
    padding: 12px 28px;
    border-radius: 50px
}
.now i{
    background: #008acb;
    color: #fff;
}
.regionNav{
    display: -ms-flexbox;
    display: flex;
}
.regionNav div{
    -ms-flex: 1 1;
        flex: 1 1;
    height: 64px;
    line-height: 64px;
    text-align: center;
    background: #edf7fb;
    font-size: 30px;
}
.regionNav div.active{
    background: #fff;
    -webkit-box-shadow: inset 0 -5px 0 0 #008acb;
            box-shadow: inset 0 -5px 0 0 #008acb;
    color: #008acb;
    font-weight: bold;
}
.hot_city div span{
    display: inline-block;
    min-width: 120px;
    height: 50px;
    line-height: 50px;
    text-align: center;
    border: 1px solid #e8ecef;
    margin-bottom: 20px;
    margin-right: 32px;
    border-radius: 30px;
}
.hot_city{
    padding: 0 30px;
    font-size: 28px;
    overflow: hidden;
}
.hot_city>div{
    padding-top: 16px;  
    margin-right: -38px;
}

.wrap{
    overflow: hidden;
}
.cityTit{
    padding: 12px;
    height: 25px;
    font-size: 22px;
    text-align: center;
    background-color: #edf7fb;
    position: relative;
    z-index: 3;
}
.cityTit + div{
    padding: 0 30px;
}
.cityTit + div div{
    height: 0px;
    border-bottom:1px solid #f9f9f9;
    margin-bottom: -1px;
    overflow: hidden;
    -webkit-transition: .3s ease-in;
    -o-transition: .3s ease-in;
    transition: .3s ease-in;
    font-size: 28px;
}
#cityShow + div div{
    height: 74px;
    line-height: 74px;
}
#cityHide + div div{
    height: 0;
    -webkit-transition: .2s;
    -o-transition: .2s;
    transition: .2s;
}




