

.clearfix:before,.clearfix:after{content: '';display: block;}  
.clearfix:after{clear: both;}  

.content{
    background: #f9f9f9;
    margin-bottom: 98px;
}
.recommend{
    padding-top: 22px;
    position: relative;
}
.recommend_box{
    margin-left: 19px;
    margin-bottom: 14px;
    padding: 22px 24px 22px 10px;
    background: #fff;
    -webkit-box-shadow: 10px 10px 10px -10px #e0e0e0;
            box-shadow: 10px 10px 10px -10px #e0e0e0;
}

.img{
    float: left;
    width: 196px;
    height: 143px;
    background-size: cover;
    background-position: center center;
}
.text{
    margin-left: 228px;
    height: 60px;
}
.text h5{
    font-size: 28px;
}
.text p{
    padding: 6px 0;
}
.text p i{
    display: inline-block;
    padding: 2px 8px;
    margin: 0 8px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border: 1px solid #d62121;
}
.text p i:nth-child(1){
    background: #d62121;
    color: #fff;
}
.text p span{
    float: right;
    text-decoration: line-through;
    font-size: 18px;
}
.text div{
    height: 32px;
    line-height: 32px;
    font-size: 24px;
}
.text div i{
    color: #e5004a;
}
.text div span{
    font-size: 36px;
    float: right;
    font-weight: bold;
    color: #e5004a;
}
.numLine{
    height: 60px;
    text-align: center;
    overflow: hidden;
}
.numLine div{
    margin-top: 30px;
    margin-bottom: -30px;
    height: 1px;
    background-image: -webkit-gradient(linear,left top, right top,from(#f3f3f3),color-stop(#e1e1e1),to(#f3f3f3));
    background-image: -webkit-linear-gradient(left,#f3f3f3,#e1e1e1,#f3f3f3);
    background-image: -o-linear-gradient(left,#f3f3f3,#e1e1e1,#f3f3f3);
    background-image: linear-gradient(to right,#f3f3f3,#e1e1e1,#f3f3f3);
}
.numLine p{
    display: inline-block;
    height: 60px;
    line-height: 60px;
    margin-bottom: -30px;
    padding: 0 10px;   
    background: #f9f9f9; 
    font-size: 22px;
}
.item{
    padding: 30px 22px;
    background: #fff;
    border-bottom: 1px solid #e8ecef;
}
.re_tit{
    font-family: 'iconfont';
    position: absolute;
    left: 0;top: 18px;
    background: #5cb4dd;
    
    font-size: 24px;
    color: #fff;
}
.re_tit p{
    background: #5cb4dd;
    padding: 6px 26px;
    position: relative;
    z-index: 3;
}
.re_tit:after{
    content:'';
    width: 18px;
    height: 10px;
    background: #0a7eb4;
    position: absolute;
    left: 0;
    top: 84%;
    -webkit-transform: rotateZ(24deg) skew(23deg) ;
        -ms-transform: rotate(24deg) skew(23deg);
            transform: rotateZ(24deg) skew(23deg) ;
    z-index: 1;
}
.re_tit i{
    margin-right: 10px;
}


