
.filter{
    font-family: 'iconfont';
    position: relative;
    
}
.filter h5{
    height: 64px;
    line-height: 64px;
    background-color: #d5ecf7;
    font-size: 24px;
    font-weight: normal;
    padding: 0 30px;
}
.filter h5 em{
    font-family: 'iconfont';
    float: right;
}
.filter h5 i{
    font-family: 'iconfont';
    font-size: 20px;
}
.filter ul{
    background: #fff;
}
.filter li {
    margin-left: 30px;
    margin-right: 30px;
    line-height: 72px;
    border-bottom: 1px solid #f3f5f7;
    font-size: 28px;
    margin-bottom: -1px;
}
.filter li span{
    margin-right: 22px;
    visibility: hidden;
}
.filter li.active{
    color: #008acb;
}
.filter li.active span{
    visibility: visible;
}
.filter div{
    width: 100%;
    height: 0;
    overflow: hidden;
    background: rgba(0,0,0,.5);
    position: absolute;
    left: 0;
    top: 65px;
    -webkit-transition: .3s ;
    -o-transition: .3s ;
    transition: .3s ;
    z-index: 66;
}
.conditionShow{
    display: block;
}
.conditionHide{
    display: none;
}
.tran em{
    -webkit-transform: rotate(90deg);
        -ms-transform: rotate(90deg);
            transform: rotate(90deg);
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
}
.tran + div{
    -webkit-animation: height .3s forwards ease-in-out;
            animation: height .3s forwards ease-in-out;
    /* animation-fill-mode:  */
}
@-webkit-keyframes height {
    0%{
        height: 0;
    }
    100%{
        height: 1200px;
    }
}
@keyframes height {
    0%{
        height: 0;
    }
    100%{
        height: 1200px;
    }
}


